.faq {
  background-color: #fff;
  /* padding: 40px 0px; */
}

.faq_container {
  background-image: url(/ogunaward/static/media/faq.a8f7100b.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.faq_container>div {
  padding: 40px 16px;
}

.faq_container .faq_text {
  margin-bottom: 40px;
  text-align: left;
}

.faq_text p {
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -1px;
  color: var(--grey-800);
  width: 60%;
  max-width: 252px;
  margin-bottom: 20px;
}

.faq .faq_container {
  background-color: #fff;
  background-image: none;
  margin: 40px 16px;
}

/* ACCORDIN STYLE */

.accordion {
  list-style: none;
  padding: 0;
}


.accordion_item {
  margin-bottom: 32px;
}

.accordion_item .button {
  font-size: 16px;
  line-height: 22px;
  padding: 16px 24px;
  color: var(--grey-900);
  text-align: left;
  font-weight: 700;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: var(--blue-10);
  background: #F1F5F9;
  border: 1px solid #F1F5F9;
  border-radius: 12px;
}

.accordion_item.active .button {
  border-radius: 0;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.answer ul{
  list-style: disc;
}
.answerUl > li{
  margin-bottom: 8px;
}
.answerUl > li > ul{
  margin-left: 16px;
  margin-top: 8px;
}
.answerUl .innerList{
  margin-left: 16px;
  margin-top: 8px;
}
.answerUl .innerList span{
  font-style: italic;
}

.button :after {
  content: url(/ogunaward/static/media/icon+.6716c861.svg);
  /* content: "+"; */
  width: 24px;
  margin-left: 16px;
  height: 24px;
}

.accordion_item.active .button :after {
  content: url(/ogunaward/static/media/icon-.ea93fd26.svg);
  /* content: "-"; */
}

.control {
  font-size: 32px;
}

.answer {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--grey-600);
  padding: 4px 24px 24px;
  margin-left: 24px;
}

/* activate toggle */
.answer_wrapper {
  background: #F1F5F9;
  height: 0;
  overflow: hidden;
  transition: height ease 0.2s;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}


