.formHeader {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1px;
    color: var(--grey-800);
    width: 100%;
    max-width: 320px;
    margin: 0 auto 24px;
}


label,
input {
    color: var(--text-color);
}

.form_input input {
    background: #FFFFFF;
    border: 0.5px solid var(--grey-300);
    border-radius: 8px;
    width: 100%;
    height: 48px;
}


.formRow {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
}



button.back {
    margin: 24px 0;
    /* // padding: 0.3rem; */
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    /* text-align: center; */
    color: var(--grey-900);
    background: #FFFFFF;
    border: 0.5px solid var(--grey-200);
    border-radius: 8px;
    width: 96px;
    height: 40px;
}
button.back:hover{
    background-color: var(--grey-10);
}

button.back img {
    width: 24px;
    height: 24px;
}

button.back p {
    margin-top: 0;
    margin-bottom: 0;
}

.input-error {
    border-color: var(--error-color);
}

.error {
    color: var(--error-color);
    font-size: 6px;
    margin-top: 0.3rem;
}

.disabled-btn {
    cursor: not-allowed;
    background: rgba(var(--grey-400), 0.8);
}

input[type="checkbox"] {
    margin-right: 0.7rem;
}

.steps {
    display: inline-block;
    margin-bottom: 1rem;
    color: var(--text-color);
    font-size: 1.2rem;
}

.errorText {
    color: var(--error-color);
    font-size: 1.2rem;
    margin-top: 0.4rem;
    display: inline-block;
}

.hideForm {
    display: none;
}

.showForm {
    display: block;
    margin: 24px auto;
}


/* Last Form Page */
.congrats {
    max-width: 422px;
    margin: 80px auto;
    text-align: center;
}

.congratsImg {
    margin: 0 auto 42px;
    width: 106px;
    height: 106px;
}

.CongratsText {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--grey-600);

}

